@media (max-width: 992px) {
  .d-md-block {
    display: block; } }

@media (max-width: 767px) {
  .w-sm-100 {
    width: 100%; }
  .w-sm-50 {
    width: 50%; } }

header a, footer a {
  color: #FFFFFF; }

.header__logo {
  background-image: url("https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/logo.png?raw=true");
  background-repeat: no-repeat;
  background-size: cover;
  height: 49px;
  width: 80px;
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap; }
  @media (max-width: 767px) {
    .header__logo {
      width: 48px;
      height: 29px; } }

.header__menu a {
  transition: all 0.3s; }
  .header__menu a:hover {
    background-color: #650300; }
  @media (max-width: 767px) {
    .header__menu a {
      border: 1px solid #FFFFFF; } }

footer a {
  transform: translateY(0px);
  transition: .2s; }
  footer a:hover {
    transform: translateY(-4px); }

.footer__others {
  border-top: 1px solid #FFFFFF; }

@media (max-width: 992px) {
  .footer__logo {
    display: none; } }

@media (max-width: 767px) {
  .footer__menu {
    display: none; } }

.form-group {
  width: 100%;
  margin-bottom: 1rem; }
  @media (max-width: 992px) {
    .form-group {
      margin-bottom: 0.5rem; } }

label {
  font-size: 1rem;
  display: block; }

input, textarea {
  width: 100%;
  padding: 0.875rem 1rem;
  transition: all .3s; }
  input:focus, textarea:focus {
    outline-color: #AA0601;
    box-shadow: inset 1px 1px 5px rgba(0, 0, 0, 0.2); }

input[type="checkbox"] {
  display: inline-block;
  width: auto;
  margin-right: 0.25rem; }

input[type="submit"] {
  display: block;
  width: calc(100% / 2);
  font-weight: 700;
  color: #FFFFFF;
  background-color: #AA0601;
  border: none;
  margin: 0 auto;
  padding: 1rem 0; }
  input[type="submit"]:hover, input[type="submit"]:focus {
    background-color: #650300; }

.pagination {
  display: flex;
  justify-content: center; }
  .pagination__item {
    border: 1px solid #707070;
    border-right: none; }
    .pagination__item:last-child {
      border-right: 1px solid #707070; }
  .pagination__link {
    font-size: 18px;
    padding: 12px 16px;
    color: #707070;
    transition: all .3s; }
    @media (max-width: 767px) {
      .pagination__link {
        font-size: 11px;
        padding: 8px 10px; } }
    .pagination__link.--disabled {
      background-color: #F2F2F2; }
    .pagination__link.--active {
      color: #FFFFFF;
      background: #000000; }
    .pagination__link:hover {
      color: #FFFFFF;
      background-color: #707070; }

.productCard__img {
  width: 100%;
  height: 164px;
  object-fit: cover;
  transition: all .3s; }
  .productCard__img:hover {
    opacity: .7; }

.productCard__title, .productCard__price {
  text-transform: uppercase;
  font-size: 1.5rem; }
  @media (max-width: 767px) {
    .productCard__title, .productCard__price {
      font-size: 1rem; } }

.productCard__price {
  font-weight: 500;
  color: #AA0601; }

.productCard__color li {
  height: 24px;
  width: 24px;
  border-radius: 0.25rem; }
  @media (max-width: 767px) {
    .productCard__color li {
      height: 16px;
      width: 16px; } }
  .productCard__color li:first-child {
    background-color: #5F3E2D;
    margin-right: 0.5rem; }
    @media (max-width: 767px) {
      .productCard__color li:first-child {
        margin-right: 0.25rem; } }
  .productCard__color li:last-child {
    background-color: #B75929; }

.banner {
  height: 840px;
  background: url("./../images/banner.png") no-repeat center;
  background-size: cover; }
  @media (max-width: 992px) {
    .banner {
      background-position: 43% center; } }
  @media (max-width: 767px) {
    .banner {
      background: url("./../images/banner-sm.png") no-repeat center;
      background-size: cover;
      height: 394px; } }

.section__wrap {
  padding-top: 4rem;
  padding-bottom: 5rem; }
  @media (max-width: 992px) {
    .section__wrap {
      padding: 2.5rem 0; } }
  @media (max-width: 767px) {
    .section__wrap {
      padding: 2rem 0; } }

.section__title {
  font-size: 48px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2.5rem; }
  @media (max-width: 992px) {
    .section__title {
      font-size: 32px;
      margin-bottom: 2rem; } }
  @media (max-width: 767px) {
    .section__title {
      font-size: 24px;
      margin-bottom: 1rem; } }

.service__item {
  width: 20%; }
  @media (max-width: 992px) {
    .service__item {
      width: 40%; } }
  @media (max-width: 767px) {
    .service__item {
      width: 80%; } }

.service__img {
  width: 100px;
  vertical-align: initial; }

.design {
  background: url("./../images/bg.png") center; }
  .design__item {
    position: relative;
    width: calc((100% - 30px) / 2); }
    @media (max-width: 767px) {
      .design__item {
        width: 100%; } }
  .design__img::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.5);
    transition: all 0.3s; }
  .design__img:hover::before {
    background-color: rgba(255, 255, 255, 0); }
  .design__title {
    position: absolute;
    font-size: 4.5rem;
    font-weight: 700; }
    @media (max-width: 992px) {
      .design__title {
        font-size: 2rem; } }

.recommend__item {
  width: calc((100% - 90px) / 4); }
  @media (max-width: 992px) {
    .recommend__item {
      width: calc((100% - 30px) / 2);
      margin-bottom: 28px; } }
  @media (max-width: 767px) {
    .recommend__item {
      width: 100%;
      margin-bottom: 1rem; } }

.recommend__content {
  border: 1px solid #dee2e6;
  padding: 1rem; }

.recommend img {
  width: 100%; }

.contact__container {
  max-width: 730px;
  margin: 0 auto; }
  @media (max-width: 992px) {
    .contact__container {
      max-width: 450px; } }
  @media (max-width: 767px) {
    .contact__container {
      max-width: 345px; } }

.contact .checkbox {
  margin-bottom: 2.625rem; }
  @media (max-width: 992px) {
    .contact .checkbox {
      margin-bottom: 1.25rem; } }
  @media (max-width: 767px) {
    .contact .checkbox {
      margin-bottom: 1.5rem; } }

.product__menu {
  max-width: 1386px;
  margin: 0 auto; }
  .product__menu__item {
    text-align: center;
    font-size: 42px;
    width: 33.333%;
    transition: all 0.3s;
    border: 1px solid #dcdcdc;
    border-bottom: 0px;
    text-transform: uppercase;
    font-weight: 500; }
    @media (max-width: 992px) {
      .product__menu__item {
        font-size: 28px; } }
    @media (max-width: 767px) {
      .product__menu__item {
        font-size: 14px; } }
    .product__menu__item.--acitve, .product__menu__item:hover {
      box-shadow: inset 0 -16px 0px #AA0601; }
      @media (max-width: 992px) {
        .product__menu__item.--acitve, .product__menu__item:hover {
          box-shadow: inset 0 -8px 0px #AA0601; } }
      @media (max-width: 767px) {
        .product__menu__item.--acitve, .product__menu__item:hover {
          box-shadow: inset 0 -4px 0px #AA0601; } }
  .product__menu__img {
    height: 48px; }
    @media (max-width: 992px) {
      .product__menu__img {
        height: 32px; } }
    @media (max-width: 767px) {
      .product__menu__img {
        height: 15px; } }

.product__banner {
  height: 309px; }
  @media (max-width: 767px) {
    .product__banner {
      height: 144px; } }
  .product__banner img {
    object-fit: cover;
    width: calc((100% - 30px) / 2); }
    @media (max-width: 992px) {
      .product__banner img {
        width: 50%; } }

.optical {
  padding-top: 88px;
  padding-bottom: 156px; }
  @media (max-width: 992px) {
    .optical {
